.navbar {
  background-color: #f8f8f8;
  padding: 0;
  list-style: none;
  width: 10%;
}

.navbar li {
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
}

.navbar li a {
  color: #333;
  text-decoration: none;
  font-size: 16px;
}

.navbar li a:hover {
  background-color: #e0e0e0;
}
